<template>
  <section class="publish-car-info c-gap-inner-header c-container">
    <div
      class="c-font-20 c-gap-top-20">
      新建卖车模板
    </div>
    <el-form
      class="c-gap-top-20">
      <el-row>
        <!-- 类型 -->
        <el-col
          :span="6">
          <el-form-item
            label="类型">
            <el-select
              :value="carType.value"
              placeholder="请选择类型"
              :style="{
                'width': '100%'
              }"
              @change="handleTypeChange">
              <el-option
                v-for="(item2, index2) in carType.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 价格 -->
        <el-col
          :span="6">
          <el-form-item
            label="价格">
            <el-select
              filterable
              allow-create
              v-model="price.value"
              placeholder="请选择价格"
              :style="{
                'width': '100%'
              }"
              @change="handlePriceChange">
              <el-option
                v-for="(item2, index2) in price.options"
                :key="index2"
                :label="item2.label"
                :value="item2.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 里程 -->
        <el-col
          :span="6">
          <el-form-item
            label="里程">
            <el-select
              filterable
              allow-create
              v-model="mileage.value"
              placeholder="请选择里程"
              :style="{
                'width': '100%'
              }"
              @change="handleMileageChange">
              <el-option
                v-for="(item2, index2) in mileage.options"
                :key="index2"
                :label="item2.label"
                :value="item2.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!-- 车内饰颜色 -->
        <el-col
          :span="6">
          <el-form-item
            :label="interiorColor.text">
            <el-select
              :value="interiorColor.value"
              placeholder="请选择车内饰颜色"
              :style="{
                'width': '100%'
              }"
              @change="handleInteriorColorChange">
              <el-option
                v-for="(item2, index2) in interiorColor.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 车外表颜色 -->
        <el-col
          :span="6">
          <el-form-item
            :label="exteriorColor.text">
            <el-select
              :value="exteriorColor.value"
              placeholder="请选择车外表颜色"
              :style="{
                'width': '100%'
              }"
              @change="handleExteriorColorChange">
              <el-option
                v-for="(item2, index2) in exteriorColor.options"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!-- 品牌 -->
        <el-col
          :span="6">
          <el-form-item
            label="品牌">
            <el-select
              :value="$store.state.curBrand.get('label')"
              placeholder="请选择"
              :style="{
                'width': '100%'
              }"
              @change="handleBrandsChange">
              <el-option
                v-for="(item2, index2) in $store.state.brands.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 系列 -->
        <el-col
          :span="6">
          <el-form-item
            label="系列">
            <el-select
              :value="$store.state.curSerie.get('label')"
              placeholder="请选择"
              :style="{
                'width': '100%'
              }"
              @change="handleSeriesChange">
              <el-option
                v-for="(item2, index2) in $store.state.series.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 分系列 -->
        <el-col
          :span="6">
          <el-form-item
            label="分系列">
            <el-select
              :value="$store.state.curSubserie.get('label')"
              placeholder="请选择"
              :style="{
                'width': '100%'
              }"
              @change="handleSubseriesChange">
              <el-option
                v-for="(item2, index2) in $store.state.subseries.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!-- 型号 -->
        <el-col
          :span="6">
          <el-form-item
            label="型号">
            <el-select
              :value="$store.state.curModel.get('label')"
              placeholder="请选择"
              :style="{
                'width': '100%'
              }"
              @change="handleModelsChange">
              <el-option
                v-for="(item2, index2) in $store.state.models.toJS().data"
                :key="index2"
                :label="item2.label"
                :value="index2">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <!-- 车架号 -->
        <el-col
          :span="24">
          <el-form-item
            label="车架号（17位）">
            <el-input
              :maxlength="17"
              :minlength="17"
              v-model="vin"
              placeholder="请输入车架号">
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <!-- 图片 -->
      <el-row>
        <el-col
          :span="24">
          <el-form-item
            class="c-gap-bottom-zero"
            label="上传图片（每张图片大小不超过2MB）">
          </el-form-item>
        </el-col>
        <el-col
          :span="24">
          <el-upload
            ref="photoUpload"
            action="https://jsonplaceholder.typicode.com/posts/"
            multiple
            list-type="picture-card"
            :auto-upload="false"
            :on-preview="handlePhotoPreview"
            :on-remove="handlePhotoRemove"
            :on-success="handlePhotoSuccess"
            :on-error="handlePhotoError"
            :on-progress="handlePhotoProgress"
            :before-upload="handlePhotoBeforeUpload"
            :before-remove="handlePhotoBeforeRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-col>
      </el-row>
      <!-- 车架号图片 -->
      <el-row>
        <el-col
          :span="24">
          <el-form-item
            class="c-gap-bottom-zero"
            label="上传车架号图片（每张图片大小不超过2MB）">
          </el-form-item>
        </el-col>
        <el-col
          :span="24">
          <el-upload
            ref="vinPhotoUpload"
            action="https://jsonplaceholder.typicode.com/posts/"
            multiple
            list-type="picture-card"
            :auto-upload="false"
            :on-preview="handlePhotoPreview"
            :on-remove="handlePhotoRemove"
            :on-success="handlePhotoSuccess"
            :on-error="handlePhotoError"
            :on-progress="handlePhotoProgress"
            :before-upload="handlePhotoBeforeUpload"
            :before-remove="handlePhotoBeforeRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-col>
      </el-row>
      <el-row
        class="c-gap-top-20">
        <el-col
          class="flex-center"
          :span="24">
          <el-button
            type="success"
            @click="handleSubmit">
            提交
          </el-button>
        </el-col>
      </el-row>
    </el-form>
  </section>
</template>

<script>
export default {
  name: "PublishCarInfo",
  data() {
    return {
      // 车辆类型
      carType: {
        key: "type",
        text: "类型",
        value: 0,
        options: [
          {
            label: "新车&二手车",
            value: 0
          },
          {
            label: "新车",
            value: 1,
          },
          {
            label: "二手车",
            value: 2
          },
          {
            label: "认证汽车",
            value: 3
          }
        ]
      },
      price: {
        key: "price",
        text: "价格",
        value: 100000,
        options: [
          {
            value: 50000
          },
          {
            value: 100000,
          },
          {
            value: 200000
          },
          {
            value: 300000
          },
          {
            value: 500000
          },
          {
            value: 1000000
          }
        ]
      },
      // 里程
      mileage: {
        key: "mileage",
        text: "里程",
        value: 100,
        options: [
          {
            value: 500
          },
          {
            value: 1000,
          },
          {
            value: 2000
          },
          {
            value: 3000
          },
          {
            value: 5000
          },
          {
            value: 10000
          }
        ]
      },
      // 车内饰颜色
      interiorColor: {
        key: "interior_color",
        text: "车内饰颜色",
        value: 0,
        options: [
          {
            label: "米色",
            value: 0
          },
          {
            label: "黑色",
            value: 1,
          },
          {
            label: "灰色",
            value: 2
          },
          {
            label: "其他",
            value: 11
          }
        ]
      },
      // 车外表颜色
      exteriorColor: {
        key: "exterior_color",
        text: "车外表颜色",
        value: 0,
        options: [
          {
            label: "灰色",
            value: 0
          },
          {
            label: "白色",
            value: 1,
          },
          {
            label: "黄色",
            value: 2
          },
          {
            label: "其他",
            value: 14
          }
        ]
      },
      // 车架号
      vin: ''
    };
  },
  components: {},
  created() {
    // 获得品牌
    this.$store.dispatch("getBrands", {
      vm: this
    });
  },
  mounted() {},
  methods: {
    // 类型改变
    handleTypeChange(index) {
      this.carType.value = this.carType.options[index].value;
    },
    // 价格改变
    handlePriceChange(value) {
    },
    // 里程改变
    handleMileageChange(value) {
    },
    // 车内饰颜色改变
    handleInteriorColorChange(index) {
      this.interiorColor.value = this.interiorColor.options[index].value;
    },
    // 车外表颜色改变
    handleExteriorColorChange(index) {
      this.exteriorColor.value = this.exteriorColor.options[index].value;
    },
    // 品牌改变
    handleBrandsChange(brandIndex) {
      const brandItem = this.$store.state.brands.toJS().data[brandIndex];
      console.log("brandItem", brandItem);
      this.$store.commit("setStateData", {
        ...brandItem,
        stateName: "curBrand"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getSeries", {
        brandId: brandItem.id,
        vm: this
      });
    },
    // 系列改变
    handleSeriesChange(seriesIndex) {
      const seriesItem = this.$store.state.series.toJS().data[seriesIndex];
      this.$store.commit("setStateData", {
        ...seriesItem,
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getSubseries", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: seriesItem.id,
        vm: this
      });
    },
    // 分系列改变
    handleSubseriesChange(subseriesIndex) {
      const subseriesItem = this.$store.state.subseries.toJS().data[subseriesIndex];
      this.$store.commit("setStateData", {
        ...subseriesItem,
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: '',
        stateName: "curModel"
      });
      this.$store.dispatch("getModels", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: this.$store.state.curSerie.toJS().value,
        subseriesId: subseriesItem.id,
        vm: this
      });
    },
    // 型号改变
    handleModelsChange(modelIndex) {
      const modelItem = this.$store.state.models.toJS().data[modelIndex];
      this.$store.commit("setStateData", {
        ...modelItem,
        stateName: "curModel"
      });
    },
    handlePhotoPreview() {
      console.log('handlePhotoPreview');
    },
    handlePhotoRemove() {
      console.log('handlePhotoRemove');
    },
    handlePhotoSuccess() {
      console.log('handlePhotoSuccess');
    },
    handlePhotoError() {
      console.log('handlePhotoError');
    },
    handlePhotoProgress() {
      console.log('handlePhotoProgress');
    },
    handlePhotoBeforeUpload() {
      console.log('handlePhotoBeforeUpload');
    },
    handlePhotoBeforeRemove() {
      console.log('handlePhotoBeforeRemove');
    },
    handleVinPhotoPreview() {
      console.log('handleVinPhotoPreview');
    },
    handleVinPhotoRemove() {
      console.log('handleVinPhotoRemove');
    },
    // 提交
    handleSubmit() {
      // 要上传的数据
      const data = {};
      // 类型
      data.type = this.carType.value;
      // 价格
      data.price = this.price.value;
      // 里程
      data.mileage = this.mileage.value;
      // 内饰颜色
      data.interior_color = this.interiorColor.value;
      // 外表颜色
      data.exterior_color = this.exteriorColor.value;
      // 型号
      data.model_id = this.$store.state.curModel.get('value');
      if (!data.model_id) {
        this.$message({
          showClose: true,
          message: '请选择型号',
          type: 'error'
        });
        return;
      }
      // 车架号
      data.vin = this.vin;
      if (!data.vin) {
        this.$message({
          showClose: true,
          message: '请填写车架号',
          type: 'error'
        });
        return;
      }
      if (data.vin.length !== 17) {
        this.$message({
          showClose: true,
          message: '车架号的长度应为17位',
          type: 'error'
        });
        return;
      }
      // 图片
      const uploadPhoto = this.$refs.photoUpload;
      if (uploadPhoto.uploadFiles.length === 0) {
        this.$message({
          showClose: true,
          message: '请上传图片',
          type: 'error'
        });
        return;
      }
      data.photo = uploadPhoto.uploadFiles[0].raw;
      // 车架号图片
      const vinPhotoUpload = this.$refs.vinPhotoUpload;
      if (vinPhotoUpload.uploadFiles.length === 0) {
        this.$message({
          showClose: true,
          message: '请上传车架号图片',
          type: 'error'
        });
        return;
      }
      data.vin_photo = vinPhotoUpload.uploadFiles[0].raw;
      console.log('要上传的数据 data', data);
      this.$store.dispatch("uploadCarInfo", {
        ...data,
        vm: this
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.flex-center {
  display: flex;
  justify-content: center;
  align-content: center;
}

.publish-car-info {

  .filter-item {
    display: inline-block;
  }

  .divider {
    margin-bottom: 20px;
    height: 1px;
    background: $grey200;
  }

  .btn-layout {
    display: flex;
    justify-content: center;

    .btn-right {
      margin-left: 100px;
    }
  }
}
</style>



