<template>
  <section
    class="car-card"
    @click.stop="handleClick">
    <!--左边的图-->
    <div
      class="left-img"
      :style="{
        'background-image': 'url('+ imgSrc +')'
      }">
    </div>
    <!--中间-->
    <section
      class="middle c-gap-inner-left-20 c-gap-inner-right-20"
      :style="{
        'width': !isCustom ? '100%' : ''
      }">
      <h2 class="row1">
        {{item.type.value}}
        {{item.model.year}}
        {{item.model.subseries.series.brand.name}}
        {{item.model.name}}
        {{item.model.arrangement}}
        {{item.model.subseries.name}}
        {{item.model.subseries.series.name}}
      </h2>
      <div class="row2">
        <!-- 价格 -->
        <span class="c-font-big">￥{{item.price}}</span>
      </div>
      <div class="row4 c-gap-top-20">
        <span>
          <span class="c-color-grey">里程数：</span>
          {{item.mileage}}公里
        </span>
      </div>
      <div class="row5">
        <span>
          <span class="c-color-grey">车身颜色：</span>
          {{item.exterior_color.value}}
        </span>
        <span>
          <span class="c-color-grey">内饰颜色：</span>
          {{item.interior_color.value}}
        </span>
      </div>
      <div class="row6">
        <span>
          <span class="c-color-grey">油耗：</span>
          {{item.model.kpl}}L
        </span>
      </div>
      <!--收藏-->
      <div
        v-if="isCustom"
        class="star c-btn"
        @click="handleStarClick">
        <i
          v-if="isStar"
          class="el-icon-star-on">
        </i>
        <i
          v-if="!isStar"
          class="el-icon-star-off">
        </i>
        收藏
      </div>
    </section>
    <!--右侧-->
    <section
      v-if="isCustom"
      class="right c-gap-inner-left-20 c-gap-inner-right-20">
      <div class="c-gap-bottom-20">车行信息：{{item.dealer.company_name}} {{item.dealer.address}}</div>
      <div>联系方式：{{item.dealer.phone}}</div>
    </section>
  </section>
</template>

<script>
export default {
  name: "CarCard",
  props: {
    isCustom: {
      type: Boolean,
      default: true
    },
    item: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      // 测试数据
      mockData: {
        id: "18d5d3eb6741467cb16be739e9f01409",
        // 型号
        model: {
          id: 208,
          // 分系列
          subseries: {
            id: 77,
            // 系列
            series: {
              id: 35,
              // 品牌
              brand: {
                id: 5,
                name: "大众",
                eng_name: null // 英文名
              },
              name: "全新朗逸"
            },
            name: "1.6L", // 系列宣传语
            body_style: 1,
            tank: "55.0", // 油箱大小；单位L
            // 单位是毫米
            height: 1460, // 车高度
            length: 4605, // 车长度
            width: 1765, // 车宽度
            axle: 2610 // 轴距
          },
          name: "手动舒适版",
          engine: "1.6", // 引擎排量；单位L
          turbo: false, // 是否是涡轮增压
          arrangement: "L4", // 引擎排列；L4、V6、W12、V8
          year: 2018, // 年份
          transmission: 0, // 排档；手动/自动
          drivetrain: 0, // 驱动；四驱、前驱、后驱
          fuel: 0, // 燃油型号；汽油、柴油、油电混合、电气
          seating: 5, // 座位数
          door: "4", // 门
          msrp: 0, // 官方建议售价；目前没有数据，默认为0
          kpl: "4.9", // 油耗
          tire: "", // 轮胎型号尺寸；例如：R17 255/255
          // 以下格式为数组
          interior: null, // 内饰
          exterior: null, // 外饰
          safety: null, // 安全性
          control: null, // 控制性
          option: null, // 可选配置；可选天窗、可选自动车窗
          url:
            "http://brand.vw.com.cn/d5carsetting/html/132/196/configuration.html" // 配置详情链接
        },
        // 车行信息经销商
        dealer: {
          user: {
            id: 2,
            email: "johnny@gmail.com",
            address: null,
            phone: null,
            photo: null
          },
          company_name: "利星行",
          review: "0.0", // 评分
          address: "金华市义乌市城北路68号",
          phone: "12344442222",
          photo: null
        },
        vin: "4JGDF6EE6HA961162", // 车架号；每一辆的唯一id
        exterior_color: 0, // 车外表颜色
        interior_color: 2, // 车内饰颜色
        is_import: false, // 是否是进口车
        photo: [], // 图集
        type: 0, // 是新车还是二手车
        price: 230000, // 价格
        mileage: 50, // 公里数
        created_at: "2018-04-09T15:47:41.859681+08:00",
        updated_at: "2018-04-09T15:47:41.859712+08:00",
        sold_at: null, // 卖出时间
        stock: null, // 库存号；车行自己管理的库存号
        description: "", // 描述；可以让车主在这里自己写
        pending: false, // 是否在审核中
        available: true, // 车是否存在；是否卖出
        // 以下都是自定义，区别于标准配置
        tire: null, // 自定义：轮胎型号尺寸；例如：R17 255/255
        interior: [], // 自定义：内饰
        exterior: [], // 自定义：外饰
        safety: [], // 自定义：安全性
        control: [], // 自定义：控制性
        feature: [] // 自定义：亮点信息
      },
      // 默认图
      defaultImgSrc:
        "http://img.hb.aicdn.com/aaab09042b57e0a1def2b0db78b0d0a724a0e38d15c47a-WdWpa0_fw658",
      title: "新款 2016一汽-大众 CC 1.8T",
      price: "17.70万起",
      officialPrice: "18.9万起", // 官方售价
      distance: "5公里", // 距离
      isStar: false
    };
  },
  computed: {
    imgSrc() {
      if (this.item && this.item.photo && this.item.photo[0] && this.item.photo[0].file) {
        return this.item.photo[0].file;
      }
      return this.defaultImgSrc;
    }
  },
  methods: {
    handleStarClick() {
      this.isStar = !this.isStar;
      this.$emit("onStarClick");
    },
    handleClick() {
      this.$emit("click");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.car-card {
  display: flex;
  padding: 20px 0 20px 20px;
  border: 1px solid $secondColor;
  background: #fff;
  cursor: pointer;

  .left-img {
    background: center no-repeat;
    background-size: cover;
    width: 280px;
    height: 160px;
  }

  .middle {
    position: relative;
    width: 300px;

    .row2,
    .row5,
    .row4,
    .row6 {
      display: flex;
      justify-content: space-between;
    }

    .star {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      top: -12px;
      right: -12px;
      background: #fff;
      font-size: 12px;

      & i {
        font-size: 18px;
      }
    }
  }

  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid $secondColor;
  }
}
</style>



