<template>
  <section class="search">
    <!-- 第一行 -->
    <div
      v-if="isShowTopBar"
      class="nav c-gap-inner-left-20">
      <div
        v-for="(item, index) in searchTab.list"
        :key="index"
        class="nav-item c-btn transition-animate"
        :class="{
          'selected': searchTab.curSelectedIndex === index
        }"
        @click="handleTagsClick({
          item,
          index
        })">
        {{item.text}}
      </div>
    </div>
    <!-- 搜索表单 -->
    <el-row
      class="search-form"
      :style="{
        'padding-top': isShowTopBar ? '0' : '15px'
      }">
      <!-- 品牌 -->
      <el-col
        :span="6">
        <el-select
          :value="$store.state.curBrand.get('label')"
          placeholder="请选择品牌"
          :style="{
            'width': '100%'
          }"
          @change="handleBrandsChange">
          <el-option
            v-for="(item2, index2) in $store.state.brands.toJS().data"
            :key="index2"
            :label="item2.label"
            :value="index2">
          </el-option>
        </el-select>
      </el-col>
      <!-- 系列 -->
      <el-col
        :span="6">
        <el-select
          :value="$store.state.curSerie.get('label')"
          placeholder="请选择系列"
          :style="{
            'width': '100%'
          }"
          @change="handleSeriesChange">
          <el-option
            v-for="(item2, index2) in $store.state.series.toJS().data"
            :key="index2"
            :label="item2.label"
            :value="index2">
          </el-option>
        </el-select>
      </el-col>
      <!-- 分系列 -->
      <el-col
        :span="6">
        <el-select
          :value="$store.state.curSubserie.get('label')"
          placeholder="请选择分系列"
          :style="{
            'width': '100%'
          }"
          @change="handleSubseriesChange">
          <el-option
            v-for="(item2, index2) in $store.state.subseries.toJS().data"
            :key="index2"
            :label="item2.label"
            :value="index2">
          </el-option>
        </el-select>
      </el-col>
      <!-- 型号 -->
      <el-col
        :span="6">
        <el-select
          :value="$store.state.curModel.get('label')"
          placeholder="请选择型号"
          :style="{
            'width': '100%'
          }"
          @change="handleModelsChange">
          <el-option
            v-for="(item2, index2) in $store.state.models.toJS().data"
            :key="index2"
            :label="item2.label"
            :value="index2">
          </el-option>
        </el-select>
      </el-col>
      <!-- 价格 -->
      <el-col
        :span="6">
        <el-select
          v-model="price.value"
          placeholder="请选择价格"
          filterable
          allow-create
          :style="{
            'width': '100%'
          }">
          <el-option
            v-for="(item2, index2) in price.options"
            :key="index2"
            :label="item2.label"
            :value="item2.value">
          </el-option>
        </el-select>
      </el-col>
      <!-- 新车、二手车 -->
      <el-col
        :span="6">
        <el-select
          v-model="carType.value"
          placeholder="请选择类型"
          :style="{
            'width': '100%'
          }">
          <el-option
            v-for="(item2, index2) in carType.options"
            :key="index2"
            :label="item2.value"
            :value="item2.value">
          </el-option>
        </el-select>
      </el-col>
      <!-- 距离 -->
      <el-col
        :span="6">
        <el-select
          v-model="distance.value"
          filterable
          allow-create
          placeholder="请选择距离"
          :style="{
            'width': '100%'
          }">
          <el-option
            v-for="(item2, index2) in distance.options"
            :key="index2"
            :label="item2.label"
            :value="item2.value">
          </el-option>
        </el-select>
      </el-col>
      <!-- 搜索按钮 -->
      <el-col
        :span="6">
        <el-button
          type="success"
          :style="{
            'width': '100%'
          }"
          @click="handleSearchClick">
          开始搜索
        </el-button>
      </el-col>
    </el-row>
  </section>
</template>

<script>
import axios from "axios";
import httpConfig from "../../configs/httpConfig";
import { fromJS } from "immutable";

export default {
  name: "Search",
  props: {
    // 是否显示上方 bar
    isShowTopBar: {
      type: Boolean,
      default: true
    },
    // 是否直接请求
    isDirectRequest: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      searchTab: {
        curSelectedIndex: 0,
        list: [
          {
            text: "品牌搜索"
          }
        ]
      },
      // 车辆类型
      carType: {
        key: "type",
        text: "汽车类型",
        value: "新车&二手车",
        options: [
          {
            value: "新车&二手车"
          },
          {
            value: "新车"
          },
          {
            value: "二手车"
          },
          {
            value: "认证汽车"
          }
        ]
      },
      price: {
        key: "price",
        text: "价格",
        value: "",
        options: [
          {
            label: "不限",
            value: null
          },
          {
            value: 50000
          },
          {
            value: 100000
          },
          {
            value: 200000
          },
          {
            value: 300000
          },
          {
            value: 500000
          },
          {
            value: 1000000
          }
        ]
      },
      // 距离
      distance: {
        key: "distance",
        text: "距离",
        value: null,
        options: [
          {
            label: "不限",
            value: null
          },
          {
            value: 10
          },
          {
            value: 20
          },
          {
            value: 30
          },
          {
            value: 50
          },
          {
            value: 100
          },
          {
            value: 200
          },
          {
            value: 300
          },
          {
            value: 500
          },
          {
            value: 1000
          }
        ]
      }
    };
  },
  computed: {},
  created() {
    // 获得品牌
    this.$store.dispatch("getBrands", {
      vm: this
    });
  },
  methods: {
    handleTagsClick(obj) {
      this.searchTab.curSelectedIndex = obj.index;
    },
    // 品牌改变
    handleBrandsChange(brandIndex) {
      const brandItem = this.$store.state.brands.toJS().data[brandIndex];
      console.log("brandItem", brandItem);
      this.$store.commit("setStateData", {
        ...brandItem,
        stateName: "curBrand"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curModel"
      });
      this.$store.dispatch("getSeries", {
        brandId: brandItem.id,
        vm: this
      });
    },
    // 系列改变
    handleSeriesChange(seriesIndex) {
      const seriesItem = this.$store.state.series.toJS().data[seriesIndex];
      this.$store.commit("setStateData", {
        ...seriesItem,
        stateName: "curSerie"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curModel"
      });
      this.$store.dispatch("getSubseries", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: seriesItem.id,
        vm: this
      });
    },
    // 分系列改变
    handleSubseriesChange(subseriesIndex) {
      const subseriesItem = this.$store.state.subseries.toJS().data[subseriesIndex];
      this.$store.commit("setStateData", {
        ...subseriesItem,
        stateName: "curSubserie"
      });
      this.$store.commit("setStateData", {
        value: "",
        stateName: "curModel"
      });
      this.$store.dispatch("getModels", {
        brandId: this.$store.state.curBrand.toJS().value,
        seriesId: this.$store.state.curSerie.toJS().value,
        subseriesId: subseriesItem.id,
        vm: this
      });
    },
    // 型号改变
    handleModelsChange(modelIndex) {
      const modelItem = this.$store.state.models.toJS().data[modelIndex];
      this.$store.commit("setStateData", {
        ...modelItem,
        stateName: "curModel"
      });
    },
    // 搜索按钮点击
    handleSearchClick() {
      // 要筛选的数据
      const data = {};
      // 品牌
      data.brand = this.$store.state.curBrand.get("value");
      // 系列
      data.series = this.$store.state.curSerie.get("value");
      // 分系列
      data.subseries = this.$store.state.curSubserie.get("value");
      // 型号
      data.model = this.$store.state.curModel.get("value");
      console.log("search data", data);
      // 触发事件
      this.$emit("searchBtnClick");
      // 跳转至列表页
      this.$router.push({
        path: "/carlist",
        query: {
          ...data
        }
      });
      if (this.isDirectRequest) {
        // 请求列表数据
        this.$store.dispatch("fetchCars", {
          ...data,
          vm: this,
          page: 1
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";
.search {
  font-size: 14px;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  .nav {
    display: flex;
    align-items: center;
    height: 80px;
    line-height: 80px;
    background: $primaryColor;

    .nav-item {
      width: 150px;
      height: 38px;
      line-height: 38px;
      border: 1px solid #fff;
      box-sizing: border-box;

      &:hover {
        background: #fff;
        color: #000;
      }
    }

    .selected {
      position: relative;
      background: #fff;
      color: #000;

      &::after {
        position: absolute;
        content: "";
        bottom: -20px;
        left: 63px;
        width: 0;
        height: 0;
        border-width: 10px 12px;
        border-style: solid;
        border-color: #fff transparent transparent transparent;
      }
    }
  }
  .search-form {
    flex-wrap: wrap;
    padding: 0 20px 15px 20px;
    background: $primaryColor;
    box-sizing: border-box;
  }
}
</style>



