<template>
  <div id="app">
    <m-header></m-header>
    <router-view/>
    <m-footer></m-footer>
  </div>
</template>

<script>
import MHeader from "./containers/MHeader/MHeader.vue";
import MFooter from "./containers/MFooter/MFooter.vue";

export default {
  name: "app",
  components: {
    MHeader,
    MFooter
  },
  data() {
    return {
      tabSelected: 0,
      tabList: [
        {
          text: "Index",
          router: "/index"
        }
      ]
    };
  },
  created() {
    this.getUserLocation();
  },
  methods: {
    // 获得用户地理位置
    getUserLocation() {
      if (navigator.geolocation) {
        console.log("支持地理位置 api");
        navigator.geolocation.getCurrentPosition(
          position => {
            console.log("获得用户地理位置 position", position);
          },
          positionError => {
            console.error("获得用户地理位置 positionError", positionError);
          },
          {}
        );
      }
    }
  }
};
</script>

<style lang="scss">
@import "assets/reset.scss";
@import "assets/app.scss";

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: $backgroundColor;
}
// 全局设置 select 的边框为正方形
.el-input__inner {
  border-radius: 0;
}
// 全局设置 button 的边框为正方形
.el-button {
  border-radius: 0;
}
</style>



