<template>
  <section class="filter c-gap-inner-header">
    <!--轮播-->
    <!-- <el-carousel height="600px">
      <el-carousel-item
        v-for="(item, index) in banners"
        :key="index">
        <div
          class="img"
          :style="{
            'background-image':'url('+item.imgSrc+')'
          }">
        </div>
      </el-carousel-item>
    </el-carousel> -->
    <!--版心-->
    <section class="c-container">
      <!--下方-->
      <section class="bottom">
        <!--左侧筛选-->
        <!-- <filter-left></filter-left> -->
        <!--右侧-->
        <section class="right">
          <!--搜索-->
          <search
            :is-show-top-bar="false"
            :is-direct-request="true"
            class="search c-gap-inner-top-20">
          </search>
          <!--顶部筛选-->
          <!-- <filter-top></filter-top> -->
          <!--下部内容-->
          <section class="content c-gap-top-20">
            <div
              v-if="$store.state.cars.get('results').toJS().length > 0">
              <car-card
                class="c-gap-top-20"
                v-for="(item, index) in $store.state.cars.get('results').toJS()"
                :key="item.id"
                :item="item"
                @click="handleCarCardClick({item, index})">
              </car-card>
              <!-- 分页组件 -->
              <el-pagination
                class="c-gap-top-20"
                :pager-count="11"
                layout="prev, pager, next"
                :total="$store.state.cars.get('count')"
                @current-change="handleCurrentPageChange">
              </el-pagination>
            </div>
            <div
              v-else
              class="no-data">
              暂无内容
            </div>
          </section>
        </section>
      </section>
    </section>
  </section>
</template>

<script>
import Search from '../../components/Search/Search.vue';
import FilterTop from "./FilterTop/FilterTop.vue";
import FilterLeft from "./FilterLeft/FilterLeft.vue";
import CarCard from "../../components/CarCard/CarCard.vue";

export default {
  name: "CarList",
  data() {
    return {};
  },
  components: {
    FilterTop,
    FilterLeft,
    CarCard,
    Search
  },
  created() {
    const carsLength = this.$store.state.cars.get("results").toJS().length;
    console.log('this.$route.query', this.$route.query);
    // 请求列表数据
    this.$store.dispatch("fetchCars", {
      ...this.$route.query,
      vm: this,
      page: 1
    });
  },
  mounted() {
  },
  methods: {
    handleCarCardClick(item, index) {
      // this.$router.push("/cardetail", {
      //   id: 111
      // });
      this.$router.push({
        path: "/cardetail",
        query: {
          carId: item.item.id
        }
      });
    },
    // 当前页数改变触发
    handleCurrentPageChange(currentPage, b) {
      console.log('handleCurrentPageChange', currentPage, b);
      // 请求列表数据
      this.$store.dispatch("fetchCars", {
        vm: this,
        page: currentPage
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.card-item:nth-child(2n) {
  background: $grey100;
}

.el-carousel__item .img {
  width: 100%;
  height: 100%;
  background: center no-repeat;
  background-size: cover;
}

.filter {
  .bottom {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    .right {
      margin-left: 20px;
      .content {
        .no-data {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 600px;
          border: 1px solid #eee;
          box-sizing: border-box;
          font-size: 26px;
        }
      }
    }
  }
}
</style>



