<template>
  <section class="header-wrapper2">
    <section class="header-wrapper">
      <div class="header-wrapper-right"></div>
      <header class="header">
        <!--左侧-->
        <div
          class="left c-gap-left-large c-btn"
          @click="handleHeaderLeftClick">
          <div class="logo">
            logo
          </div>
          <div class="left-right c-gap-left">
            <div class="row1">百家车</div>
            <div class="row2 c-gap-top-small">slogan</div>
          </div>
        </div>
        <!--右侧-->
        <nav class="nav">
          <!--导航按钮-->
          <div
            class="nav-item"
            v-for="(item, index) in navList"
            :key="index"
            @mouseenter="handleMouseEnter(index)"
            @mouseleave="handleMouseLeave(index)"
            @click="handleNavItemClick(item, index)">
            {{item.text}}
            <transition name="fade">
              <div
                class="mask"
                v-show="item.isShowMask">
                <div
                  v-for="(item2, index2) in item.children"
                  :key="index2"
                  class="mask-item c-gap-inner-left-large transition-animate"
                  @click="handleMaskItemClick({item2, index})">
                  {{item2.text}}
                </div>
              </div>
            </transition>
          </div>
          <!--已登录-->
          <div
            v-if="$store.state.loginInfo.get('token')"
            class="login c-btn"
            @mouseenter="handleLoginMouseEnter()"
            @mouseleave="handleLoginMouseLeave()">
            <div
              class="avatar"
              :style="{
                'background-image': 'url(' + userInfo.avatar + ')'
              }"
              @click="handleAvatarClick">
            </div>
            <div class="text c-gap-top-small">
              下午好，{{$store.state.loginInfo.get('username')}}
            </div>
            <!--浮层-->
            <transition name="fade">
              <div
                class="mask"
                v-show="userInfo.isShowMask">
                <div
                  v-for="(item, index) in userInfo.children"
                  :key="index"
                  class="mask-item c-gap-inner-left-large transition-animate"
                  @click="handleLoginMaskClick(item, index)">
                  {{item.text}}
                </div>
              </div>
            </transition>
          </div>
          <!--登录注册-->
          <div
            v-else
            class="login">
            <i class="iconfont icon-round-user-new"></i>
            <div class="text c-gap-top-small">
              <span
                class="sign-in transition-animate c-btn"
                @click="handleSignInClick">
                登录
              </span>/<span
              class="sign-up transition-animate c-btn"
              @click="handleSignUpClick">
                注册
              </span>
            </div>
          </div>
        </nav>
        <!--金融计算器模态框-->
        <financial-calculator-modal
          v-if="isShowFinancialCalculatorModal"
          class="financial-calculator"
          @close="handleFinancialCalculatorModalClose">
        </financial-calculator-modal>
        <!--登录注册模态框-->
        <login-modal
          v-if="$store.state.loginModalOpts.get('isShow')"
          :type="$store.state.loginModalOpts.get('type')"
          @close="handleLoginModalClose">
        </login-modal>
      </header>
    </section>
  </section>
</template>

<script>
import FinancialCalculatorModal from "../FinancialCalculatorModal/FinancialCalculatorModal.vue";
import LoginModal from "../LoginModal/LoginModal.vue";
import httpConfig from "../../configs/httpConfig";
import { fromJS } from "immutable";
import axios from "axios";

export default {
  name: "Header",
  data() {
    return {
      isShowFinancialCalculatorModal: false,
      // 登录模态框
      loginModal: {
        isShow: true,
        type: 0
      },
      navList: [
        {
          text: "买车",
          isShowMask: false,
          children: [
            {
              text: "汽车列表",
              link: "/carlist"
            },
          ]
        },
        {
          text: "卖车",
          isShowMask: false,
          children: [
            {
              text: "发布卖车",
              link: "/publishcarinfo"
            },
          ]
        },
        {
          text: "金融",
          isShowMask: false,
          children: [
            {
              text: "金融计算器"
            },
            {
              text: "贷款申请",
              link: "/loanapplication"
            },
            // {
            //   text: "经销商"
            // }
          ]
        }
      ],
      // 用户信息
      userInfo: {
        avatar:
          "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1524426948309&di=9d470c0d9a5b5ace16af541531a5ee4e&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01460b57e4a6fa0000012e7ed75e83.png%401280w_1l_2o_100sh.png",
        isShowMask: false,
        children: [
          // {
          //   text: "寻车笔记",
          //   link: "/personinfo"
          // },
          {
            text: "发布卖车",
            link: "/publishcarinfo"
          },
          // {
          //   text: "车行管理",
          //   link: "/garagemanage"
          // },
          // {
          //   text: "物流订单",
          //   link: "/logisticsorder"
          // },
          {
            id: "logout",
            text: "退出登录"
          }
        ]
      }
    };
  },
  created() {
    console.log("created this.$store.state", this.$store.state);
  },
  computed: {
    isLogin() {
      return this.$store.state.loginInfo.get("token");
    }
  },
  methods: {
    // nav 点击
    handleNavItemClick(item, index) {
      console.log('handleNavItemClick item', item, 'index', index);
      if (item.text === '买车') {
        this.$router.push("/carlist");
      } else if (item.text === '卖车') {
        this.$router.push("/publishcarinfo");
      }
    },
    // 鼠标移入事件
    handleMouseEnter(index) {
      this.navList[index].isShowMask = true;
    },
    // 鼠标移出事件
    handleMouseLeave(index) {
      this.navList[index].isShowMask = false;
    },
    // 登录区域鼠标移入事件
    handleLoginMouseEnter() {
      this.userInfo.isShowMask = true;
    },
    // 登录区域鼠标移出事件
    handleLoginMouseLeave() {
      this.userInfo.isShowMask = false;
    },
    // 登录点击事件
    handleSignInClick() {
      this.$store.commit("setLoginModalOpts", {
        ...this.$store.state.loginModalOpts.toJS(),
        isShow: true,
        type: 0
      });
    },
    // 注册点击事件
    handleSignUpClick() {
      this.$store.commit("setLoginModalOpts", {
        ...this.$store.state.loginModalOpts.toJS(),
        isShow: true,
        type: 1
      });
    },
    // 左边点击事件
    handleHeaderLeftClick() {
      this.$router.push("/index");
    },
    // 蒙层项点击事件
    handleMaskItemClick(obj) {
      const { item2, index } = obj;
      if (item2.text === "金融计算器") {
        this.isShowFinancialCalculatorModal = true;
        return;
      }
      if (item2.link) {
        this.$router.push(item2.link);
      }
      // 关闭浮层
      this.navList[index].isShowMask = false;
    },
    // 登录项蒙层点击事件
    handleLoginMaskClick(item, index) {
      // 如果有路由，跳转
      if (item.link) {
        this.$router.push(item.link);
      }
      // 如果是登出按钮
      if (item.id === "logout") {
        this.handleLogout();
      }
      // 关闭用户信息显示浮层
      this.userInfo.isShowMask = false;
    },
    // 关闭金融计算器
    handleFinancialCalculatorModalClose() {
      this.isShowFinancialCalculatorModal = false;
    },
    // 登录模态框关闭事件
    handleLoginModalClose() {
      this.$store.commit("setLoginModalOpts", {
        ...this.$store.state.loginModalOpts.toJS(),
        isShow: false
      });
    },
    // 登出
    handleLogout() {
      this.$store.dispatch("logOut", {
        vm: this
      });
    },
    // 头像点击事件
    handleAvatarClick() {
      this.$store.dispatch("fetchUserInfo", {
        vm: this
      });
    }
  },
  components: {
    FinancialCalculatorModal,
    LoginModal
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.header-wrapper2 {
  position: fixed;
  width: 100%;
  box-shadow: 0 0 5px 1px $primaryColor;
  background: #fff;
  z-index: 8;
}

.header-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;

  .header-wrapper-right {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background: $primaryColor;
  }
}

.header {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: $contentWidth;
  height: $headerHeight;
  z-index: 9;
  background: #fff;

  .left {
    display: flex;
    align-items: center;

    .logo {
      width: 50px;
      height: 50px;
      line-height: 50px;
      border-radius: 50%;
      background: $primaryColor;
      color: #fff;
      font-size: 12px;
      text-align: center;
    }

    .left-right {
      .row1 {
        font-size: 14px;
      }

      .row2 {
        font-size: 12px;
      }
    }
  }

  .nav {
    display: flex;
    align-items: center;
    height: 100%;
    color: #fff;
    text-align: center;
    font-size: 14px;

    .nav-item {
      transition: $transitionTime;
      position: relative;
      width: 120px;
      height: 100%;
      line-height: $headerHeight;
      background: $primaryColor;
      cursor: pointer;

      &:after {
        content: "";
        position: absolute;
        width: 2px;
        height: 60px;
        top: 10px;
        right: 0;
        background: $secondColor;
      }

      .mask {
        position: absolute;
        width: 120px;
        top: $headerHeight;
        box-sizing: border-box;
        border-left: 1px solid $secondColor;
        border-right: 1px solid $secondColor;
        border-bottom: 1px solid $secondColor;
        z-index: 9;
        background: #fff;
        color: #000;

        .mask-item {
          height: 40px;
          line-height: 40px;
          text-align: left;

          &:hover {
            background: $secondColor;
            color: #fff;
          }
        }
      }
    }

    .login {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 120px;
      height: 100%;
      background: $primaryColor;

      .iconfont {
        font-size: 22px;
      }

      .avatar {
        flex-shrink: 0;
        display: inline-block;
        height: 30px;
        width: 30px;
        border-radius: 50%;
        background: no-repeat center;
        background-size: cover;
      }

      .text {
        font-size: 12px;

        span:hover {
          opacity: 0.7;
        }
      }

      .mask {
        position: absolute;
        width: 120px;
        top: $headerHeight;
        box-sizing: border-box;
        border-left: 1px solid $secondColor;
        border-right: 1px solid $secondColor;
        border-bottom: 1px solid $secondColor;
        z-index: 9;
        background: #fff;
        color: #000;

        .mask-item {
          height: 40px;
          line-height: 40px;
          text-align: left;

          &:hover {
            background: $secondColor;
            color: #fff;
          }
        }
      }
    }
  }
}
</style>



