<template>
  <modal @close="handleClose">
    <section
      class="login-modal"
      v-loading="$store.state.loginInfo.get('isLoading')">
      <!-- 上方切换 tab 栏 -->
      <div class="title c-font-big">
        <div
          class="tab-sign-in c-btn "
          :class="{
            'selected':currentTab === 0
          }"
          @click="handleTabChange(0)">
          登录
        </div>
        <div class="divider c-gap-left-small c-gap-right-small">|</div>
        <div
          class="tab-sign-up c-btn"
          :class="{
            'selected':currentTab === 1
          }"
          @click="handleTabChange(1)">
          注册
        </div>
      </div>
      <!-- 下方表单栏 -->
      <section class="bottom sign-in">
        <!--账号-->
        <section class="form-item">
          <div class="c-font-big">电子邮箱 / 手机号码</div>
          <el-input
            id="username"
            class="c-gap-top"
            v-model="form.username"></el-input>
          <div
            v-if="currentTab === 0"
            class="row-right c-gap-top">
            <span class="link c-btn">
              忘记用户名？
            </span>
          </div>
        </section>
        <!--密码-->
        <section class="form-item c-gap-top-20">
          <div class="c-font-big">密码</div>
          <el-input
            id="password"
            class="c-gap-top"
            type="password"
            v-model="form.password">
          </el-input>
          <!-- 忘记密码 -->
          <div
            v-if="currentTab === 0"
            class="row-right c-gap-top">
            <span class="link c-btn">
              忘记密码？
            </span>
          </div>
          <!-- 密码提示语 -->
          <div
            v-if="currentTab === 1"
            class="c-color-grey c-gap-top">
            (6个字/数字以上，不包括空格)
          </div>
        </section>
        <!--确认密码-->
        <section
          v-if="currentTab === 1"
          class="form-item c-gap-top-20">
          <div class="c-font-big">确认密码</div>
          <el-input
            id="passwordAgain"
            class="c-gap-top"
            type="password"
            v-model="form.passwordAgain">
          </el-input>
        </section>
        <!-- 登录 -->
        <el-button
          v-if="currentTab === 0"
          class="btn-sign-in"
          type="success"
          @click="handleSignIn">
          登录
        </el-button>
        <!-- 注册 -->
        <el-button
          v-if="currentTab === 1"
          class="btn-sign-in"
          type="success"
          @click="handleSignUp">
          注册
        </el-button>
      </section>
    </section>
  </modal>
</template>

<script>
import Modal from "../../components/Modal/Modal.vue"; // 对话框
import httpConfig from "../../configs/httpConfig"; // http 配置
import axios from "axios"; // 请求库
import Qs from "qs"; // 将对象转换为 query string
import StoreJs from "store"; // 存储 js
import { storageConstant } from "../../constant.js";

export default {
  name: "LoginModal",
  props: {
    // 当前状态；模态框类型，0登录，1为注册
    type: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      currentTab: 0,
      isLoading: false,
      form: {
        username: "",
        password: "",
        passwordAgain: ""
      }
    };
  },
  created() {
    this.currentTab = this.type;
  },
  components: {
    Modal
  },
  methods: {
    // 触发关闭
    handleClose() {
      this.$emit("close");
    },
    // Tab 切换
    handleTabChange(index) {
      this.currentTab = index;
    },
    // 登录
    handleSignIn() {
      this.$store.dispatch("signIn", {
        username: this.form.username,
        password: this.form.password,
        client_id: httpConfig.client_id,
        grant_type: "password",
        vm: this
      });
    },
    // 注册
    handleSignUp() {
      this.$store.dispatch("signUp", {
        email: this.form.username,
        password: this.form.password,
        vm: this
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.login-modal {
  padding: 0 20px 40px;
  width: 500px;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;

  .title {
    display: flex;
    justify-content: center;

    .tab-sign-in,
    .tab-sign-up {
      padding: 0 10px;
    }

    .selected {
      background: $primaryColor;
      color: #fff;
    }
  }

  .bottom {
    margin-top: 60px;

    .form-item {
      position: relative;

      .row-right {
        position: absolute;
        bottom: -30px;
        right: 0;
        text-align: right;

        .link {
          color: $lightBlue500;
        }
      }
    }

    .btn-sign-in {
      margin-top: 60px;
      width: 100%;
    }
  }
}
</style>



