import Vue from 'vue';
import Vuex from 'vuex';
import {fromJS} from 'immutable';
import httpConfig from "../configs/httpConfig"; // http 配置
import axios from "axios"; // 请求库
import Qs from "qs"; // 将对象转换为 query string
import StoreJs from "store"; // 存储 js
import {storageConstant} from "../constant.js"; // 常量

// 使用 Vuex
Vue.use(Vuex);

// 从 LocalStorage/Cookies 里取出数据
const loginInfo = StoreJs.get(storageConstant.loginInfo);
console.log('store initial loginInfo', StoreJs.get(storageConstant.loginInfo));

// 如果在模块化构建系统中，请确保在开头调用了 Vue.use(Vuex)
export default new Vuex.Store({
  /**
   * state
   */
  state: {
    // 登录信息
    loginInfo: fromJS({
      isLoading: false, // 是否加载中
      token: loginInfo && loginInfo.token, // 认证信息
      username: loginInfo && loginInfo.username, // 用户名
    }),
    // 用户信息
    userinfo: fromJS({
      id: -1,
      uuid: '',
      email: '',
      address: '',
      phone: '',
      photo: ''
    }),
    // 车行信息
    dealerinfo: fromJS({
      user: null,
      company_name: '',
      review: '',
      address: '',
      phone: '',
      photo: ''
    }),
    // 登录框配置
    loginModalOpts: fromJS({
      isShow: false, // 是否显示
      type: 0 // 类型：0为登录；1为注册
    }),
    // banners
    banner: fromJS({
      count: 0, // 数量
      next: null,
      previous: null,
      results: [] // 列表数据
    }),
    // cars 汽车列表
    cars: fromJS({
      count: 0, // 数量
      next: null,
      previous: null,
      results: [] // 列表数据
    }),
    // 汽车详情
    carInfo: fromJS({
      "id": "",
      "model": {
        "id": -1,
        "subseries": {
          "id": -1,
          "series": {
            "id": -1,
            "brand": {
              "id": -1,
              "name": "",
              "eng_name": null
            },
            "name": ""
          },
          "body_style": {
            "id": -1,
            "value": ""
          },
          "name": "",
          "tank": "",
          "height": -1,
          "length": -1,
          "width": -1,
          "axle": -1
        },
        "transmission": {
          "id": 0,
          "value": ""
        },
        "drivetrain": {
          "id": 0,
          "value": ""
        },
        "fuel": {
          "id": 0,
          "value": ""
        },
        "name": "",
        "engine": "",
        "turbo": false,
        "arrangement": "",
        "year": 0,
        "seating": 0,
        "door": "",
        "msrp": 0,
        "kpl": "",
        "tire": "",
        "interior": null,
        "exterior": null,
        "safety": null,
        "control": null,
        "option": null,
        "url": ""
      },
      "dealer": {
        "user": {
          "id": -1,
          "uuid": "",
          "email": "",
          "address": null,
          "phone": null,
          "photo": null
        },
        "company_name": "",
        "review": "",
        "address": "",
        "phone": "",
        "photo": null
      },
      "vin": "",
      "type": {},
      "exterior_color": {},
      "interior_color": {},
      "is_import": false,
      "photo": [],
      "price": 0,
      "mileage": 0,
      "created_at": "",
      "updated_at": "",
      "sold_at": null,
      "stock": null,
      "description": "",
      "pending": false,
      "available": true,
      "tire": null,
      "interior": [],
      "exterior": [],
      "safety": [],
      "control": [],
      "feature": []
    }),
    // 上传的汽车信息
    uploadCarInfo: fromJS({
    }),
    // 当前选中品牌
    curBrand: fromJS({id: ''}),
    // 品牌列表
    brands: fromJS({data: []}),
    // 当前选中系列
    curSerie: fromJS({id: ''}),
    // 系列列表
    series: fromJS({data: []}),
    // 当前选中分系列
    curSubserie: fromJS({id: ''}),
    // 分系列列表
    subseries: fromJS({data: []}),
    // 当前选中型号
    curModel: fromJS({id: ''}),
    // 型号列表
    models: fromJS({data: []})
  },
  /**
   * mutations
   */
  mutations: {
    // 通用 mutations
    setStateData(state, data) {
      const {stateName} = data;
      delete data.stateName;
      state[stateName] = fromJS(data);
    },
    // 设置登录信息
    setLoginInfo(state, loginInfo) {
      state.loginInfo = fromJS(loginInfo);
    },
    // 设置登录框配置
    setLoginModalOpts(state, data) {
      state.loginModalOpts = fromJS(data);
    },
    // 设置 banner
    setBanner(state, data) {
      state.banner = fromJS(data);
    }
  },
  /**
   * action
   */
  actions: {
    // 用户登录
    signIn({
      commit
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 改变 loading 状态
      commit('setLoginInfo', {isLoading: true});
      // 登录请求
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/auth/signin/`,
        transformRequest: [data => Qs.stringify(data)],
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data
      }).then(response => {
        console.log("signIn response", response);
        commit('setLoginInfo', {isLoading: false});
        if (response.status !== 200 && response.status !== 201) {
          vm.$message({message: '登录失败', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          vm.$message({message: '登录失败', type: 'error', showClose: true});
          return;
        }
        vm.$message({message: '登录成功', type: 'success', showClose: true});
        // 登录成功 拿到登录数据
        const loginInfo = response.data.data;
        // 改变 loginInfo
        commit('setLoginInfo', loginInfo);
        // 关闭登录对话框
        commit('setLoginModalOpts', {isShow: false});
        // 保存数据
        StoreJs.set(storageConstant.loginInfo, loginInfo);

        // 请求个人信息
        vm
          .$store
          .dispatch("fetchUserInfo", {vm: vm});

        // 请求车行信息
        vm
          .$store
          .dispatch("fetchDealerInfo", {vm: vm});
      }).catch(error => {
        // 改变 loading 状态
        commit('setLoginInfo', {isLoading: false});
        vm.$message({message: `登录失败${error}`, type: 'error', showClose: true});
        console.error(error);
      });
    },
    // 用户注册
    signUp({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 改变 loading 状态
      commit('setLoginInfo', {isLoading: true});
      // 注册请求
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/auth/signup/`,
        transformRequest: [data => Qs.stringify(data)],
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data
      }).then(response => {
        console.log("signUp response", response);
        commit('setLoginInfo', {isLoading: false});
        if (response.status !== 200 && response.status !== 201) {
          vm.$message({message: '注册失败', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          vm.$message({message: '注册失败', type: 'error', showClose: true});
          return;
        }
        // 注册成功
        vm.$message({message: '注册成功', type: 'success', showClose: true});
        commit('setLoginModalOpts', {isShow: false});
        // 调用登录接口
        dispatch('signIn', {
          username: data.email,
          password: data.password,
          client_id: httpConfig.client_id,
          grant_type: "password",
          vm: vm
        });
      }).catch(error => {
        console.error('error', error);
        if (error.response.data.errno === 4) {
          // 改变 loading 状态
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: `注册失败，该账号已存在`, type: 'error', showClose: true});
        }
      });
    },
    // 登出
    logOut({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/auth/logout/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("signUp response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: '登出失败', type: 'error', showClose: true});
          return;
        }
        if (response.data.errno !== 0) {
          commit('setLoginInfo', {isLoading: false});
          vm.$message({message: '登出失败', type: 'error', showClose: true});
          return;
        }
        // 登出成功
        vm.$message({message: '登出成功', type: 'success', showClose: true});
        commit('setLoginInfo', {
          token: '',
          username: ''
        });
        // 保存数据
        StoreJs.set(storageConstant.loginInfo, null);
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', error);
      });
    },
    // 首页广告列表
    getIndexAD({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/index_ad/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getIndexAD response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'banner'
        });
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 汽车品牌列表
    getBrands({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_brands/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getBrands response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '所有品牌',
          label: '所有品牌',
          value: ''
        });
        commit('setStateData', {data, stateName: 'brands'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 汽车系列列表
    getSeries({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_series/?brand=${data.brandId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getSeries response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '所有系列',
          label: '所有系列',
          value: ''
        });
        commit('setStateData', {data, stateName: 'series'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 汽车分系列列表
    getSubseries({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_subseries/?brand=${data.brandId}&series=${data.seriesId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getSubseries response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '所有分系列',
          label: '所有分系列',
          value: ''
        });
        commit('setStateData', {data, stateName: 'subseries'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 汽车分系列列表
    getModels({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      console.log('getModels data', data);
      delete data.vm;
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/get_models/?brand=${data.brandId}&series=${data.seriesId}&subseries=${data.subseriesId}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("getModels response", response);
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response
          .data
          .data
          .map(item => {
            item.label = item.name;
            item.value = item.id;
            return item;
          });
        data.unshift({
          id: -1,
          name: '所有型号',
          label: '所有型号',
          value: ''
        });
        commit('setStateData', {data, stateName: 'models'});
      }).catch(error => {
        console.error('error', error);
      });
    },
    // 汽车列表
    fetchCars({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 如果页数不存在，默认置为1
      if (!data.page) {
        data.page = 1;
      }
      console.log('fetchCars data', data);
      let url = `${httpConfig.serverHost}/cars/for_sale/search_result/?page=${data.page}`;
      if (data.brand) {
        url += `&brand=${data.brand}`;
      }
      if (data.series) {
        url += `&series=${data.series}`;
      }
      if (data.subseries) {
        url += `&subseries=${data.subseries}`;
      }
      if (data.model) {
        url += `&model=${data.model}`;
      }
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
      }).then(response => {
        console.log("fetchCars response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'cars'
        });
        window.scrollTo(0, 0);
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', error);
      });
    },
    // 汽车详情
    fetchCarInfoById({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/cars/detail/${data.id}`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(response => {
        console.log("fetchCarInfoById response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'carInfo'
        });
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', error);
      });
    },
    // 个人中心
    fetchUserInfo({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 拿到 token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/mycar/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          "Authorization": `Bearer ${token}`
        }
      }).then(response => {
        console.log("fetchUserInfo response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'userinfo'
        });
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg) {
          vm.$message({message: `个人中心接口失败，${error.response.data.errmsg}`, type: 'error', showClose: true});
        }
      });
    },
    // 车行信息
    fetchDealerInfo({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 拿到 token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "get",
        url: `${httpConfig.serverHost}/mycar/dealer_info/`,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          "Authorization": `Bearer ${token}`
        }
      }).then(response => {
        console.log("fetchDealerInfo response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'dealerinfo'
        });
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg) {
          vm.$message({message: `获取车行信息接口失败，${error.response.data.errmsg}`, type: 'error', showClose: true});
        }
      });
    },
    // 汽车上传
    uploadCarInfo({
      commit,
      dispatch
    }, data) {
      // 保留 vm 实例
      const vm = data.vm;
      delete data.vm;
      // 拿到 token
      const token = vm
        .$store
        .state
        .loginInfo
        .get('token');
      // 创建 formData
      const formData = new FormData();
      formData.append('type', data.type);
      formData.append('price', data.price);
      formData.append('mileage', data.mileage);
      formData.append('interior_color', data.interior_color);
      formData.append('exterior_color', data.exterior_color);
      formData.append('model_id', data.model_id);
      formData.append('vin', data.vin);
      formData.append('photo', data.photo);
      formData.append('vin_photo', data.vin_photo);
      // 打开全屏 loading
      const loading = vm.$loading({lock: true});
      axios({
        method: "post",
        url: `${httpConfig.serverHost}/mycar/upload_car/`,
        headers: {
          "Content-Type": "multipart/form-data",
          "Authorization": `Bearer ${token}`
        },
        data: formData
      }).then(response => {
        console.log("uploadCarInfo response", response);
        // 关闭全局 loading
        loading.close();
        if (response.status !== 200 && response.status !== 201) {
          return;
        }
        if (response.data.errno !== 0) {
          return;
        }
        vm.$message({message: '上传成功', type: 'success', showClose: true});
        const data = response.data.data;
        commit('setStateData', {
          ...data,
          stateName: 'uploadCarInfo'
        });
        // 获取到上传成功的汽车 id
        const carId = data.id;
        // 跳转至该详情页
        vm.$router.push({
          path: "/cardetail",
          query: {
            carId
          }
        });
      }).catch(error => {
        // 关闭全局 loading
        loading.close();
        console.error('error', JSON.stringify(error));
        if (error && error.response && error.response.data && error.response.data.errmsg && error.response.data.errmsg.type) {
          vm.$message({message: `汽车上传接口失败，${error.response.data.errmsg.type}`, type: 'error', showClose: true});
        }
      });
    }
  }
});



