<!-- 车行管理 -->
<template>
    <section class="c-gap-inner-header c-container">
        <el-row class="c-gap-top-40">
            <el-col :span="4">
                <el-menu>
                    <el-menu-item
                        v-for="(item, index) in navList"
                        :key="index"
                        :index="index + ''">
                        <span slot="title">{{item.text}}</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
            <el-col
                class="c-gap-inner-left-20"
                :span="20">
                <template
                  v-for="(item, index) in cars">
                  <car-card
                      v-if="item"
                      :key="index"
                      :is-custom="false"
                      class="c-gap-bottom-20">
                  </car-card>
                </template>
            </el-col>
        </el-row>
    </section>
</template>

<script>
import CarCard from "../../components/CarCard/CarCard.vue";

export default {
  name: "GarageManage",
  data() {
    return {
      navList: [
        {
          text: "车行信息"
        },
        {
          text: "上传车源"
        },
        {
          text: "库存管理"
        },
        {
          text: "店铺展示"
        },
        {
          text: "预约拖车"
        },
        {
          text: "收藏"
        }
      ],
      cars: []
    };
  },
  components: {
    CarCard
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";
</style>



