<template>
  <section class="index c-gap-inner-header">
    <!--轮播-->
    <el-carousel
      height="600px">
      <el-carousel-item
        v-for="(item, index) in $store.state.banner.toJS().results"
        :key="index">
        <div class="img"
             :style="{'background-image':'url('+item.file+')'}"></div>
      </el-carousel-item>
    </el-carousel>
    <!--版心-->
    <section class="c-container">
      <!-- 搜索 wrapper -->
      <div class="search-wrapper">
        <!--搜索-->
        <search
          class="search c-gap-inner-top-20">
        </search>
      </div>
    </section>
  </section>
</template>

<script>
import MFooter from "../MFooter/MFooter.vue";
import Search from "../../components/Search/Search.vue";

export default {
  name: "Index",
  data() {
    return {
      banners: [
        {
          imgSrc:
            "http://desk.fd.zol-img.com.cn/t_s1600x900c5/g5/M00/0F/09/ChMkJlauza6IOuzjAAQRQ6b4SjAAAH9HQJb4NcABBFb471.jpg"
        },
        {
          imgSrc:
            "http://desk.fd.zol-img.com.cn/t_s1920x1080c5/g5/M00/0F/09/ChMkJ1auza6ICkGRAAJCuW6kUvcAAH9HQJp9QgAAkLR000.jpg"
        },
        {
          imgSrc:
            "http://desk.fd.zol-img.com.cn/t_s1920x1200c5/g5/M00/0F/09/ChMkJlauza6IQed-ABe8YwuAco8AAH9HQLHsPAAF7x7859.jpg"
        }
      ]
    };
  },
  components: {
    Search,
    MFooter
  },
  created() {
    // 获取首页广告轮播图
    this.$store.dispatch("getIndexAD", {
      vm: this
    });
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";
.card-item:nth-child(2n) {
  background: $grey100;
}
.el-carousel__item .img {
  width: 100%;
  height: 100%;
  background: center no-repeat;
  background-size: cover;
}
.index {
  .search-wrapper {
    position: relative;
    height: 140px;
    .search {
      position: absolute;
      top: -100px;
      margin: 0 auto;
      z-index: 2;
    }
  }
}
</style>



